<template>
    <div-row :obj="obj" class="content-wrapper text-picture">
        <el-row :gutter="obj.gutter">
            <el-col>
                <h1 class="title" v-html="obj.title" :class="obj.animateTitle? 'wow '+obj.animateTitle : ''"></h1>
                <p class="desc" v-html="obj.desc" :class="obj.animateText? 'wow '+obj.animateText : ''" ></p>
            </el-col>
            <el-col :span="24/obj.columns" v-for="(item,index) in obj.imgTextBox" :key="index">
                <div
                :style="{
                    paddingLeft: item.paddingLeft+'px',
                    paddingRight: item.paddingRight+'px',
                    paddingTop: item.paddingTop+'px',
                    paddingBottom: item.paddingBottom+'px'}"
                @click="goClick(item.httpUrl)" :class="obj.animateImg? 'wow '+obj.animateImg : ''">                  
                    <div class="img image">
                        <img :src="item.imgUrl">
                    </div>
                    <div class="text-box">
                        <div class="textBox">
                            <h4 class="text-title text-center" v-html="item.title"></h4>
                            <h5 class="text-desc text-center" v-html="item.desc"></h5>
                            <p class="text-info" v-html="item.info"></p>  
                        </div>                     
                    </div>
                </div>
            </el-col>
        </el-row>     
    </div-row>
</template>
<script>
import animate from '@/mixins/animate'
export default {
    name: 'TwoColumns',
    mixins: [animate]
    
}
</script>
<style lang="less" scoped>
    .content-wrapper{
        .title,.desc{
            text-align: center
        }
        .title{
            font-size: 40px;
            color:#666;
            padding: 10px 0;
        }
        .desc{
            color:#bdc3c7;
            font-size: 16px;
        }
        .image{
            margin: 0 auto;
            width: 100%;
            padding: 20px;
            display: flex;
            justify-content: center;
            align-items: center;  
            img{
                max-width: 100%;
                height: auto;
                display: block;
            }         
        }
        .text-box{
            .text-title,.text-desc{
                text-align: left;
                overflow: hidden;
                text-overflow:ellipsis;
            }
            .text-title{
                white-space: nowrap;
            }
            .text-desc{
                display: -webkit-box;
                -webkit-line-clamp: 5;
                -webkit-box-orient: vertical;
                line-height: 20px;
                min-height: 60px;
                font-size: 13px;
            }
        }
    }
</style>



