<template>
    <el-tabs
        :style="{
            width: obj.width+'%',
            padding: obj.padding+'px'
        }" 
       :tab-position="obj.tabPosition" 
        v-model="obj.curTab" type="border-card">
        <el-tab-pane 
        :style="{ height: obj.height?obj.height+'px': 'auto'}"
        v-for="(column, key, columnIndex) in obj.childList" :key="columnIndex" 
        :label="(obj.tabList[columnIndex]).label" 
        :name="(obj.tabList[columnIndex]).value">
            <el-form label-width="60px">                         
                <draggable class="child-drag-box" tag="li" id="baseTab" @change="changeList" :list="column" @add="addEnd" v-bind="{group:{ name:'base' }, dragClass: 'dragClass'}">                                   
                    <div v-for="(child, childIndex) in column" :key="childIndex" class="child-component">
                        <span class="row-remove label" @click="delComponent(key,childIndex)"><i class="iconfont icon-shanchu"></i> 删除</span>
                        <span class="row-edit label" @click="setComponent(key, childIndex)"><i class="iconfont icon-editor"></i> 编辑</span>
                        <base-button :obj="child" v-if="child.type=='BaseButton'"></base-button>
                        <base-img :obj="child" v-if="child.type=='BaseImg'"></base-img>
                        <base-title :obj="child" v-if="child.type=='BaseTitle'"></base-title>
                        <base-p :obj="child" v-if="child.type=='BaseP'"></base-p>
                        <base-slider :obj="child" v-if="child.type=='BaseSlider'"></base-slider>
                        <form-id :obj="child" v-if="child.type=='FormId'"></form-id>
                        <form-input :obj="child" v-if="child.type=='FormInput'"></form-input>
                        <form-textarea :obj="child" v-if="child.type=='FormTextarea'"></form-textarea>
                        <form-radio :obj="child" v-if="child.type=='FormRadio'"></form-radio>
                        <form-select :obj="child" v-if="child.type=='FormSelect'"></form-select>
                        <form-checkbox :obj="child" v-if="child.type=='FormCheckbox'"></form-checkbox>
                        <title :obj="child" v-if="child.type=='Title'"></title>
                        <form-checkbox :obj="child" v-if="child.type=='FormCheckbox'"></form-checkbox>
                        <banner :obj="child" v-if="child.type=='Banner'"></banner>
                        <compony-team :obj="child" v-if="child.type=='ComponyTeam'"></compony-team>
                        <cover-img :obj="child" v-if="child.type=='CoverImg'"></cover-img>
                        <icon-box :obj="child" v-if="child.type=='IconBox'"></icon-box>
                        <icon-columns :obj="child" v-if="child.type=='IconColumns'"></icon-columns>
                        <nav-bar :obj="child" v-if="child.type=='NavBar'"></nav-bar>
                        <seam-img-scroll :obj="child" v-if="child.type=='SeamImgScroll'"></seam-img-scroll>
                        <seam-text-scroll :obj="child" v-if="child.type=='SeamTextScroll'"></seam-text-scroll>
                        <steps :obj="child" v-if="child.type=='Steps'"></steps>
                        <swiper :obj="child" v-if="child.type=='Swiper'"></swiper>
                        <text-bottom-img :obj="child" v-if="child.type=='TextBottomImg'"></text-bottom-img>
                        <text-top-img :obj="child" v-if="child.type=='TextTopImg'"></text-top-img>
                        <text-left-img :obj="child" v-if="child.type=='TextLeftImg'"></text-left-img>
                        <text-right-img :obj="child" v-if="child.type=='TextRightImg'"></text-right-img>
                        <three-columns :obj="child" v-if="child.type=='ThreeColumns'"></three-columns>
                        <three-columns2 :obj="child" v-if="child.type=='ThreeColumns2'"></three-columns2>
                        <three-columns3 :obj="child" v-if="child.type=='ThreeColumns3'"></three-columns3>
                        <two-columns :obj="child" v-if="child.type=='TwoColumns'"></two-columns>
                        <time-line :obj="child" v-if="child.type=='TimeLine'"></time-line>

                    </div>   
                </draggable>
            </el-form>  
        </el-tab-pane>
    </el-tabs>             
</template>

<script>
import draggable from 'vuedraggable'
import FormInput from '@/components/template/form/FormInput'
import FormSelect from '@/components/template/form/FormSelect'
import FormRadio from '@/components/template/form/FormRadio'
import FormCheckbox from '@/components/template/form/FormCheckbox'
import FormTextarea from '@/components/template/form/FormTextarea'
import Title from '@/components/template/module/Title'
import BaseImg from '@/components/template/baseCss/BaseImg'
import BaseP from '@/components/template/baseCss/BaseP'
import BaseButton from '@/components/template/baseCss/BaseButton'
import BaseSlider from '@/components/template/baseCss/BaseSlider'
import BaseTab from '@/components/template/baseCss/BaseTab'
import FormId from '@/components/template/FormId'
import CoverImg from '@/components/template/module/CoverImg'
import Swiper from '@/components/template/module/Swiper'
import NavBar from '@/components/template/module/NavBar'
import TextLeftImg from '@/components/template/module/TextLeftImg'
import TextRightImg from '@/components/template/module/TextRightImg'
import TextTopImg from '@/components/template/module/TextTopImg'
import TextBottomImg from '@/components/template/module/TextBottomImg'
import Banner from '@/components/template/module/Banner'
import TwoColumns from '@/components/template/module/TwoColumns'
import ThreeColumns from '@/components/template/module/ThreeColumns'
import ThreeColumns2 from '@/components/template/module/ThreeColumns2'
import ThreeColumns3 from '@/components/template/module/ThreeColumns3'
import SeamImgScroll from '@/components/template/module/SeamImgScroll'
import SeamTextScroll from '@/components/template/module/SeamTextScroll'
import IconBox from '@/components/template/module/IconBox'
import IconColumns from '@/components/template/module/IconColumns'
import Steps from '@/components/template/module/Steps'
import ComponyTeam from '@/components/template/module/ComponyTeam'
import TimeLine from '@/components/template/module/TimeLine'

import deepClone from '@/utils/deepClone'
export default {
    name: 'BaseTab',
    components: {
        draggable,
        FormInput,
        FormSelect,
        FormRadio,
        FormCheckbox,
        FormTextarea,
        Title,
        BaseImg,
        BaseP,
        BaseButton,
        BaseSlider,
        BaseTab,
        FormId,
        CoverImg,
        Swiper,
        NavBar,
        TextLeftImg,
        TextRightImg,
        TextTopImg,
        TextBottomImg,
        Banner,
        TwoColumns,
        ThreeColumns,
        ThreeColumns2,
        ThreeColumns3,
        SeamImgScroll,
        SeamTextScroll,
        IconBox,
        IconColumns,
        Steps,
        ComponyTeam,
        TimeLine
    },
    props: {
        obj: {
            type: Object
        }
    },

    data() {
        return {
         
        }
    },
    methods: {

        changeList() {

        },
        // 删除组件
        delComponent(key, childIndex) {
            this.obj.childList[key].splice(childIndex,1)
            this.dispatch()
        },
        // 组件设置
        setComponent(key, childIndex) {          
            this.$emit('tabset',this.obj.childList[key][childIndex])
        },
        // 保存数据到vuex 
        dispatch() {
            this.$store.dispatch('set_template_list')
        },
        addEnd(item) {
            if(item.oldIndex == 4 && item.to.id == 'baseTab') {
                alert('Tab组件内不能拖入Tab组件')
                return
            }
        }
    }
}
</script>
<style lang="less">
    .el-tabs--left.el-tabs--border-card .el-tabs__header.is-left, .el-tabs__nav-wrap.is-left.is-scrollable{
        height: 100%;
    }
</style>
