<template>
    <div class="setComponent">
        <div class="form-item">
            <label>增加卡片</label>
            <el-button type="primary" round size="mini" @click="addClick">增加</el-button>
        </div>
        <div class="form-item">
            <label>删除卡片</label>
            <el-button :disabled="obj.imgTextBox.length<=3" type="danger" round size="mini" @click="removeClick">删除</el-button>
        </div>    
        <div class="form-item">
            <label>图片介绍</label>
            <el-button size="mini" @click="titleClick('info')">编辑</el-button>
        </div>
        <div class="form-item">
            <label>卡片标题</label>
            <el-button size="mini" @click="titleClick('title')">编辑</el-button>
        </div>
        <div>
        <div class="form-item">
            <label>卡片描述</label>
            <el-button size="mini" @click="titleClick('desc')">编辑</el-button>
        </div>
        </div>
        <div class="form-item">
            <label>图标</label>
            <el-button size="small" type="primary" @click="iconClick">选取</el-button>
        </div>
        <div class="form-item">
            <label>图片</label>
            <el-button size="small" type="primary" @click="uploadClick">上传图片</el-button>
        </div>
        
        <div class="form-item">
            <label>图片大小</label>
            <el-input-number v-model="obj.imgWidth"></el-input-number>
        </div>
        <div class="form-item">
            <label>设置时间轴宽度</label>
            <el-input-number v-model="obj.timeLineWidth"></el-input-number>
        </div>
        <div class="form-item">
            <label>设置时间轴颜色</label>
            <el-select v-model="obj.timeLineColor" placeholder="请选择">
                <el-option
                    v-for="(item, index) in bgColorOptions"
                    :key="index"
                    :label="item"
                    :value="item">
                    <span class="bgColor" :style="{background: item}"></span>
                </el-option>
            </el-select>
        </div>
        <div class="form-item">
            <label>设置时间轴颜色</label>
            <el-select v-model="obj.timeLineIconBg" placeholder="请选择">
                <el-option
                    v-for="(item, index) in bgColorOptions"
                    :key="index"
                    :label="item"
                    :value="item">
                    <span class="bgColor" :style="{background: item}"></span>
                </el-option>
            </el-select>
        </div>
        <div class="form-item">
            <label>时间轴左侧动画</label>
            <el-select v-model="obj.animateImg" clearable placeholder="请选择">
                <el-option-group
                    v-for="(group,i) in options"
                    :key="i"
                    :label="group.label">
                    <el-option
                        v-for="(item,itemIndex) in group.options"
                        :key="itemIndex"
                        :label="item.label"
                        :value="item.value">
                    </el-option>
                </el-option-group>
            </el-select>
        </div>
        <div class="form-item">
            <label>时间轴右侧动画</label>
            <el-select v-model="obj.animateTitle" clearable placeholder="请选择">
                <el-option-group
                    v-for="(group,i) in options"
                    :key="i"
                    :label="group.label">
                    <el-option
                        v-for="(item,itemIndex) in group.options"
                        :key="itemIndex"
                        :label="item.label"
                        :value="item.value">
                    </el-option>
                </el-option-group>
            </el-select>
        </div>
    </div>
</template>
<script>
import { animateOptions } from '@/utils/animateOptions'
import { columnOptions, bgColorOptions } from '@/utils/selectOptions'
export default {
    name: 'ThreeColumns',
    props: {
        obj: {
            type: Object,

        }
    },
    data() {
        return {
            options: animateOptions,
            columnOptions: columnOptions,
            bgColorOptions: bgColorOptions
        }
    },
    methods: {
        butClick(msg) {
            this.$emit('butClick',msg)
        },
        uploadClick() {
            this.$emit('uploadClick',this.obj)
        },
        addClick() {
            this.obj.imgTextBox.push(JSON.parse(JSON.stringify(this.obj.imgTextBox[0])))
        },
        removeClick() {
            this.obj.imgTextBox.pop()
        },
        titleClick(type) {
            this.$emit('titleClick',[this.obj,type])
        },
        iconClick() {
            this.$message.info('功能开发中')
        }
    }
}
</script>
<style lang="less">

</style>

