                        <h5 class="text-desc" v-html="item.desc"></h5>                   
                    </el-col>
                </el-row>
            </el-col>
        </el-row>     
    </div-row>
</template>
<script>
import animate from '@/mixins/animate'
export default {
    name: 'TwoColumns',
    mixins: [animate]
    
}
</script>
