    .content-wrapper{
        .title,.desc{
            text-align: center
        }
        .title{
            font-size: 40px;
            color:#666;
            padding: 10px 0;
        }
        .desc{
            color:#bdc3c7;
            font-size: 16px;
            padding: 10px 0;
        }
        .team-box{
